'use client';
import React, { useState, useRef } from 'react';
import { Cliente, DetalleServicio, VentaServicio } from '../../../entities/venta';
import { useVentas } from '../../../hooks/useVentas';
import { formatCurrency } from '../../../lib/logistica/formatters';

const clientes: Cliente[] = [
  { idCliente: 'CL-001', nombre: 'ABC Soluciones', telefono: '987654321', correo: 'abc@empresa.com' },
  { idCliente: 'CL-002', nombre: 'XYZ Market', telefono: '999888777', correo: 'xyz@empresa.com' },
];

const servicios = [
  { idServicio: 'SVC-001', nombre: 'Instalaci√≥n de red', costo: 200 },
  { idServicio: 'SVC-002', nombre: 'Soporte t√©cnico', costo: 120 },
  { idServicio: 'SVC-003', nombre: 'Cableado estructurado', costo: 300 },
];

type Props = {
  open: boolean;
  onClose: () => void;
};

const VentaRegistroModal = ({ open, onClose }: Props) => {
  const { registrarVenta } = useVentas();
  const idVenta = `VENTA-${Date.now()}`;
  const fechaRegistro = new Date().toISOString();

  const [cliente, setCliente] = useState<Cliente | null>(null);
  const [detalles, setDetalles] = useState<DetalleServicio[]>([]);
  const [descuento, setDescuento] = useState(0);
  const [error, setError] = useState('');

  const agregarServicio = (id: string) => {
    const servicio = servicios.find(s => s.idServicio === id);
    if (!servicio || detalles.some(d => d.idServicio === id)) return;
    setDetalles(prev => [
      ...prev,
      {
        idDetalleServicio: `DET-${Date.now()}`,
        idServicio: servicio.idServicio,
        nombreServicio: servicio.nombre,
        costoTotal: servicio.costo,
      },
    ]);
  };

  const quitarServicio = (id: string) => {
    setDetalles(prev => prev.filter(s => s.idServicio !== id));
  };

  const total = Math.max(0, detalles.reduce((acc, s) => acc + s.costoTotal, 0) - descuento);

  const handleSubmit = () => {
    if (!cliente || detalles.length === 0) {
      setError('‚ö†Ô∏è Selecciona un cliente y al menos un servicio.');
      return;
    }

    const venta: VentaServicio = {
      idVentaServicio: idVenta,
      cliente,
      detalles,
      descuento,
      costoTotal: total,
      fechaRegistro,
    };

    registrarVenta(venta);
    onClose();
  };

  if (!open) return null;

  return (
    <div className="modal-overlay">
      <div className="modal-content max-w-2xl">
        <button className="modal-close-btn" onClick={onClose}>‚úñ</button>
        <h2 className="text-xl font-bold text-indigo-700 mb-4">‚ûï Registrar Venta</h2>

        {/* ID y Fecha */}
        <div className="grid grid-cols-1 sm:grid-cols-2 gap-3 mb-4 text-sm text-gray-600">
          <p><span className="font-semibold text-gray-800">ID Venta:</span> {idVenta}</p>
          <p><span className="font-semibold text-gray-800">Fecha:</span> {new Date(fechaRegistro).toLocaleString()}</p>
        </div>

        {/* CLIENTE */}
        <div className="mb-4">
          <label className="text-sm font-medium text-gray-700 mb-1 block">Cliente</label>
          <select
            className="w-full border rounded p-2"
            value={cliente?.idCliente || ''}
            onChange={(e) => {
              const selected = clientes.find(c => c.idCliente === e.target.value);
              if (selected) setCliente(selected);
            }}
          >
            <option value="">Seleccionar cliente</option>
            {clientes.map(c => (
              <option key={c.idCliente} value={c.idCliente}>
                {c.nombre}
              </option>
            ))}
          </select>

          {cliente && (
            <div className="text-sm text-gray-600 mt-1 space-y-1">
              <p>üìß <span className="font-medium">{cliente.correo}</span></p>
              <p>üìû <span className="font-medium">{cliente.telefono}</span></p>
            </div>
          )}
        </div>

        {/* SERVICIOS DISPONIBLES */}
        <div className="mb-6">
          <p className="text-sm font-semibold mb-2">Servicios disponibles:</p>
          <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
            {servicios.map(s => (
              <div
                key={s.idServicio}
                className="border rounded-lg p-4 bg-white shadow hover:shadow-md transition"
              >
                <h4 className="text-indigo-800 font-bold mb-1">üõ† {s.nombre}</h4>
                <p className="text-sm text-gray-600 mb-2">Costo: {formatCurrency(s.costo)}</p>
                <button
                  onClick={() => agregarServicio(s.idServicio)}
                  className="text-sm text-white bg-indigo-600 hover:bg-indigo-700 px-3 py-1 rounded-md"
                >
                  Agregar
                </button>
              </div>
            ))}
          </div>
        </div>

        {/* SERVICIOS SELECCIONADOS */}
        <div className="mb-4">
          <p className="text-sm font-medium mb-2">Servicios seleccionados:</p>
          <ul className="space-y-2">
            {detalles.map((d) => (
              <li
                key={d.idDetalleServicio}
                className="flex justify-between items-center border p-3 bg-gray-50 rounded-md"
              >
                <span>{d.nombreServicio}</span>
                <button
                  onClick={() => quitarServicio(d.idServicio)}
                  className="text-sm text-red-600 hover:underline"
                >
                  Quitar
                </button>
              </li>
            ))}
          </ul>
        </div>

        {/* DESCUENTO Y TOTAL */}
        <div className="grid grid-cols-1 sm:grid-cols-2 gap-4 items-end mb-4">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">Descuento (S/.)</label>
            <input
              type="number"
              value={descuento}
              min={0}
              onChange={(e) => setDescuento(Number(e.target.value))}
              className="border p-2 rounded w-full"
            />
          </div>
          <div className="text-right text-green-700 font-bold text-lg">
            Total: {formatCurrency(total)}
          </div>
        </div>

        {/* ERRORES */}
        {error && <p className="text-red-600 text-sm mb-3">{error}</p>}

        {/* BOTONES */}
        <div className="flex justify-end gap-3 mt-6">
          <button
            onClick={onClose}
            className="bg-gray-200 hover:bg-gray-300 text-gray-800 px-4 py-2 rounded-md transition"
          >
            Cancelar
          </button>
          <button
            onClick={handleSubmit}
            className="bg-indigo-600 hover:bg-indigo-700 text-white px-4 py-2 rounded-md transition"
          >
            Registrar
          </button>
        </div>
      </div>
    </div>
  );
};

export default VentaRegistroModal;
