import { useState, useEffect } from 'react';
import { VentaServicio } from '../entities/venta';
import { ventasMock } from '../mocks/ventas.mock';


export const useVentas = () => {
  const [ventas, setVentas] = useState<VentaServicio[]>([]);

  useEffect(() => {
    if (ventas.length === 0) {
      setVentas(ventasMock);
    }
  }, [ventas.length]);

  // Registrar una nueva venta
  const registrarVenta = (nuevaVenta: VentaServicio) => {
    setVentas(prev => [...prev, nuevaVenta]);
  };

  // Eliminar una venta por ID
  const eliminarVenta = (id: string) => {
    setVentas(prev => prev.filter(v => v.idVentaServicio !== id));
  };

  // Editar una venta ya existente
  const editarVenta = (ventaEditada: VentaServicio) => {
    setVentas(prev =>
      prev.map(v => v.idVentaServicio === ventaEditada.idVentaServicio ? ventaEditada : v)
    );
  };

  return {
    ventas,
    registrarVenta,
    eliminarVenta,
    editarVenta,
  };
};
