'use client';
import React, { useState } from 'react';
import { useVentas } from '../../../hooks/useVentas';
import { formatCurrency, formatDate } from '../../../lib/logistica/formatters';
import VentaDetalleModal from './VentaDetalleModal';
import VentaEditarModal from './VentaEditarModal';
import { VentaServicio } from '../../../entities/venta';

const VentasTable = () => {
  const { ventas, eliminarVenta, editarVenta } = useVentas();
  const [ventaSeleccionada, setVentaSeleccionada] = useState<VentaServicio | null>(null);
  const [editando, setEditando] = useState<VentaServicio | null>(null);

  const handleDelete = (venta: VentaServicio) => {
    const confirm = window.confirm(`¬øDeseas eliminar la venta ${venta.idVentaServicio}?`);
    if (confirm) {
      eliminarVenta(venta.idVentaServicio);
    }
  };

  return (
    <div className="table-container">
      <table>
        <thead>
          <tr>
            <th>ID Venta</th>
            <th>Cliente</th>
            <th>Correo</th>
            <th>Tel√©fono</th>
            <th>Fecha</th>
            <th>Servicios</th>
            <th>Total</th>
            <th>Acciones</th>
          </tr>
        </thead>
        <tbody>
          {ventas.map((venta) => (
            <tr key={venta.idVentaServicio}>
              <td>{venta.idVentaServicio}</td>
              <td>{venta.cliente.nombre}</td>
              <td>{venta.cliente.correo}</td>
              <td>{venta.cliente.telefono}</td>
              <td>{formatDate(venta.fechaRegistro)}</td>
              <td>
                <span className="tag tag-azul">{venta.detalles.length}</span>
              </td>
              <td>{formatCurrency(venta.costoTotal)}</td>
              <td>
                <div className="flex gap-2">
                  <button
                    title="Ver"
                    onClick={() => setVentaSeleccionada(venta)}
                    className="text-indigo-600 hover:text-indigo-800 text-sm font-medium"
                  >
                    üëÅÔ∏è
                  </button>
                  <button
                    title="Editar"
                    onClick={() => setEditando(venta)}
                    className="text-yellow-600 hover:text-yellow-800 text-sm font-medium"
                  >
                    ‚úèÔ∏è
                  </button>
                  <button
                    title="Eliminar"
                    onClick={() => handleDelete(venta)}
                    className="text-red-600 hover:text-red-800 text-sm font-medium"
                  >
                    üóëÔ∏è
                  </button>
                </div>
              </td>
            </tr>
          ))}
        </tbody>
      </table>

      {ventaSeleccionada && (
        <VentaDetalleModal
          venta={ventaSeleccionada}
          onClose={() => setVentaSeleccionada(null)}
        />
      )}

      {editando && (
        <VentaEditarModal
          venta={editando}
          onClose={() => setEditando(null)}
          onGuardar={(ventaActualizada) => {
            editarVenta(ventaActualizada);
            setEditando(null);
          }}
        />
      )}
    </div>
  );
};

export default VentasTable;

